<template>
  <div class="main ">
    <ul class="clear">
      <li v-for="(item,index) in clases" :key="index">
        <p>祛风除湿/骨质增生类</p>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      clases: [{ name: "祛风除湿/骨质增生类", type: 1 }]
    };
  },
  created(){
    this.clases.length=19
  },
  components: {}
};
</script>

<style lang="less" scoped>
.main {
  background: #f4f4f5;
  position: absolute;
  left: 0;
  bottom: 1.44rem;
  right: 0;
  top: 0;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
ul {
  height: 100%;
  li {
    width: 48%;
    padding: 0.533333rem 0;
    float: left;
    background: #fff;
    text-align: center;
    margin: 0.16rem 0.08rem;
    p {
      padding: 0.08rem 0;
    }
  }
}
</style>



